<template>
  <div class="bonus">
    <!-- <div class="mid" :style="$route.query.name=='佣金账户'?'height: calc(100vh - 50px);':'height: 100vh;'"> -->
    <div class="mid" style="height: calc(100% - 50px);">
      <div class="list mescroll" id="list">
        <div class="list-data" id="listData">
          <div class="item" v-for="(item,index) in list" :key="index">
            <div class="left">
              <p class="title">{{item.cname}}</p>
              <p class="text">{{item.acDes}}</p>
              <p class="time">{{item.createTime}}</p>
            </div>
            <div class="right">
              <p class="price" :style="item.acMoney < 0?'color:#333':''">{{item.acMoney >= 0?('+' + item.acMoney.toFixed(6)):(item.acMoney.toFixed(6))}}</p>
            </div>
          </div>
        </div>
      </div>
      <!-- <div class="bottom" v-if="$route.query.name=='佣金账户'" @click="withdrawal">立即提现</div> -->
      <div class="bottom" v-if="this.isBank=='1'">
        <div class="btn borderRight" @click="withdrawal('1')">立即挂卖</div>
        <div class="btn" @click="withdrawal('2')">复投</div>
      </div>
      <div v-else>
        <div class="bottom2" v-if="/红包/.test($route.query.name)" @click="e=>withdrawal2('hongbao')">红包余额</div>
        <div class="bottom2" v-if="/通证/.test($route.query.name)" @click="e=>withdrawal2('tongzheng')">通证余额</div>
      </div>
    </div>
  </div>
</template>
<script>
import { getAccountList } from "@/api";
import MeScroll from "mescroll.js";
export default {
  components: {},
  data() {
    return {
      list: [],
      isBank: '0',
      mescroll: null
    };
  },
  methods: {
    withdrawal(type) {
      this.$router.push({
        name: "withdrawal",
        query: {
          type: type,
        },
      });
    },
    withdrawal2(type) {
      this.$router.push({
        name: type === 'tongzheng' ? "paytoFriends2" : "paytoFriends",
      });
    },
    loadData(pageNum, pageSize, successCallback, errorCallback) {
      let dataObj = {
        pageNum: pageNum,
        pageSize: pageSize,
        ptCode: this.$route.query.code
      };
      getAccountList(dataObj)
        .then(res => {
          if (res.code == 0) {
            this.isBank = res.data.paytype.ptIsbank;
            successCallback && successCallback(res.data.account);

          } else {
            this.$toast.fail(res.msg);
          }
        })
        .catch(res => {
          errorCallback && errorCallback();
          console.log(res);
        });
    },
    startMescroll() {
      this.mescroll = new MeScroll("list", {
        //在vue的mounted生命周期初始化mescroll,确保此处配置的id能够被找到
        up: {
          callback: this.upCallback, //上拉回调
          //以下参数可删除,不配置
          isBounce: false, //此处禁止ios回弹,解析(务必认真阅读,特别是最后一点): http://www.mescroll.com/qa.html#q10
          page: { size: 10 }, //可配置每页8条数据,默认10
          empty: {
            //配置列表无任何数据的提示
            warpId: "listData",
            icon: require("@/assets/img/nodata.png"),
            tip: "暂无数据"
            //						  	btntext : "去逛逛 >" ,
            //						  	btnClick : function() {
            //						  		alert("点击了去逛逛按钮");
            //						  	}
          },
          noMoreSize: 10,
          scrollbar: { use: false },
          htmlNodata: '<p class="upwarp-nodata">-- 无更多数据 --</p>',
          lazyLoad: {
            use: true // 是否开启懒加载,默认false
          }
        }
      });
    },
    //上拉回调 page = {num:1, size:10}; num:当前页 ,默认从1开始; size:每页数据条数,默认10
    upCallback: function(page) {
      //联网加载数据
      let $this = this;
      $this.loadData(
        page.num,
        page.size,
        function(curPageData) {
          //curPageData=[]; //打开本行注释,可演示列表无任何数据empty的配置

          //如果是第一页需手动制空列表 (代替clearId和clearEmptyId的配置)
          // console.log(curPageData);
          if (page.num == 1) $this.list = [];
          if (curPageData.list) {
            curPageData.list.map(item => {
              $this.list.push(item);
            });
          }
          //联网成功的回调,隐藏下拉刷新和上拉加载的状态;
          //mescroll会根据传的参数,自动判断列表如果无任何数据,则提示空;列表无下一页数据,则提示无更多数据;
          //   console.log("page.num="+page.num+", page.size="+page.size+", curPageData.length="+curPageData.rows.length+", $this.goodsList.length==" + $this.pdlist.length);
          // console.log(page);

          //方法二(推荐): 后台接口有返回列表的总数据量 totalSize
          $this.mescroll.endBySize(
            curPageData.list ? curPageData.list.length : 0,
            curPageData.total
          ); //必传参数(当前页的数据个数, 总数据量)
        },
        function() {
          //联网失败的回调,隐藏下拉刷新和上拉加载的状态;
          $this.mescroll.endErr();
        }
      );
    }
  },
  mounted() {
    document.title = this.$route.query.name;
    this.startMescroll();
  }
};
</script>
<style lang="scss" scoped>
.bonus {
  height: 100%;
  .mid {
    .list {
      box-sizing: border-box;
      padding-bottom: 50px;
      .item {
        padding: 10px 15px;
        display: flex;
        align-items: center;
        border-bottom: 1pt solid #e1e1e1;
        .left {
          flex: 2;
          line-height: 25px;
          .title {
            font-size: 15px;
          }
          .text {
            color: #666;
            font-size: 15px;
          }
          .time {
            color: #999;
            font-size: 13px;
          }
        }
        .right {
          text-align: right;
          .price {
            font-size: 18px;
            color: $base-color;
          }
        }
      }
    }
    .bottom {
      width: 100%;
      position: fixed;
      display: flex;
      bottom: 50px;
      left: 0;
      color: #fff;  
      align-items: center;
      z-index: 2;
      .btn {
        flex: 1;
        text-align: center;
        background: #eb5252;
        height: 50px;
        line-height: 50px;
      }
      .borderRight {
        border-right: 1pt solid #fff;
        box-sizing: border-box;
      }
    }
    .bottom2 {
      background: #eb5252;
      position: fixed;
      bottom: 50px;
      left: 0;
      color: #fff;
      width: 100%;
      text-align: center;
      height: 50px;
      line-height: 50px;
      z-index: 2;
    }
  }
}
</style>
